@import "../../common/styles/sass/variables";
@import "../../common/styles/sass/mixins";
@import "../../common/components/button/Button.module";

.project {
  width: 450px;
  display: flex;
  flex-direction: column;
  color: $textColor;
  background-color: $bgSecondColor;


  .image {
    height: 300px;
    background-color: coral;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;

    &::before {
      position: absolute;
      content: '';
      background-color: $bgColor;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 0.4;
    }

    .viewBtn {
      @extend .btn;
      //background-color: $primaryColor;
      //padding: 1px 20px;
      //position: relative;
      z-index: 10;
      margin-top: 30px;
      //color: $textColor;
    }
  }

  .projectInfo {
    padding: 25px 20px;

    .projectTitle {
      @include text($TitleFontFamily, 16px, 1.2, 1);
      letter-spacing: 1px;
      margin: 0 0 5px;
    }

    .description {
      @include text($TextFontFamily, 12px, 1.8, 1);
      font-weight: 400;
    }
  }
}





